	module.exports = {"data":{"postsJson":{"title":"Jesus is the {Messiah/Christ}","questionsTitle":"HUNGERING TO KNOW GOD MORE","display":"block","bookName":"Mark - ΚΑΤΑ ΜΑΡΚΟΝ","ref":"8:1-9:50","html":"<p>   <strong>8</strong><u>1</u>Ἐν ἐκείναις ταῖς ἡμέραις πάλιν πολλοῦ ὄχλου ὄντος καὶ μὴ ἐχόντων τί φάγωσιν, προσκαλεσάμενος τοὺς μαθητὰς λέγει αὐτοῖς·</p><h1>JESUS </h1><h2><u>2</u>σπλαγχνίζομαι ἐπὶ τὸν ὄχλον, ὅτι ἤδη ἡμέραι τρεῖς προσμένουσίν μοι καὶ οὐκ ἔχουσιν τί φάγωσιν· <u>3</u>καὶ ἐὰν ἀπολύσω αὐτοὺς νήστεις εἰς οἶκον αὐτῶν, ἐκλυθήσονται ἐν τῇ ὁδῷ· καί τινες αὐτῶν ἀπὸ μακρόθεν ἥκασιν.</h2><p><u>4</u>καὶ ἀπεκρίθησαν αὐτῷ οἱ μαθηταὶ αὐτοῦ ὅτι</p><h1>THE DISCIPLES </h1><h3>πόθεν τούτους δυνήσεταί τις ὧδε χορτάσαι ἄρτων ἐπʼ ἐρημίας;</h3><p><u>5</u>καὶ ἠρώτα αὐτούς·</p><h1>JESUS </h1><h2>πόσους ἔχετε ἄρτους;</h2><p>οἱ δὲ εἶπαν·</p><h1>THE DISCIPLES </h1><h3>ἑπτά.</h3><p><u>6</u>καὶ παραγγέλλει τῷ ὄχλῳ ἀναπεσεῖν ἐπὶ τῆς γῆς· καὶ λαβὼν τοὺς ἑπτὰ ἄρτους εὐχαριστήσας ἔκλασεν καὶ ἐδίδου τοῖς μαθηταῖς αὐτοῦ ἵνα παρατιθῶσιν, καὶ παρέθηκαν τῷ ὄχλῳ. <u>7</u>καὶ εἶχον ἰχθύδια ὀλίγα· καὶ εὐλογήσας αὐτὰ εἶπεν καὶ ταῦτα παρατιθέναι. <u>8</u>καὶ ἔφαγον καὶ ἐχορτάσθησαν, καὶ ἦραν περισσεύματα κλασμάτων ἑπτὰ σπυρίδας. <u>9</u>ἦσαν δὲ ὡς τετρακισχίλιοι. καὶ ἀπέλυσεν αὐτούς.<br />   <u>10</u>Καὶ εὐθὺς ἐμβὰς εἰς τὸ πλοῖον μετὰ τῶν μαθητῶν αὐτοῦ ἦλθεν εἰς τὰ μέρη Δαλμανουθά.<br />   <u>11</u>Καὶ ἐξῆλθον οἱ Φαρισαῖοι καὶ ἤρξαντο συζητεῖν αὐτῷ, ζητοῦντες παρʼ αὐτοῦ σημεῖον ἀπὸ τοῦ οὐρανοῦ, πειράζοντες αὐτόν. <u>12</u>καὶ ἀναστενάξας τῷ πνεύματι αὐτοῦ λέγει·</p><h1>JESUS </h1><h2>τί ἡ γενεὰ αὕτη ζητεῖ σημεῖον; ἀμὴν λέγω ὑμῖν, εἰ δοθήσεται τῇ γενεᾷ ταύτῃ σημεῖον.</h2><p><u>13</u>καὶ ἀφεὶς αὐτοὺς πάλιν ἐμβὰς ἀπῆλθεν εἰς τὸ πέραν.<br />   <u>14</u>Καὶ ἐπελάθοντο λαβεῖν ἄρτους καὶ εἰ μὴ ἕνα ἄρτον οὐκ εἶχον μεθʼ ἑαυτῶν ἐν τῷ πλοίῳ. <u>15</u>καὶ διεστέλλετο αὐτοῖς λέγων·</p><h1>JESUS </h1><h2>ὁρᾶτε, βλέπετε ἀπὸ τῆς ζύμης τῶν Φαρισαίων καὶ τῆς ζύμης Ἡρῴδου.</h2><p><u>16</u>καὶ διελογίζοντο πρὸς ἀλλήλους</p><h1>THE DISCIPLES </h1><h3>ὅτι ἄρτους οὐκ ἔχουσιν.</h3><p><u>17</u>καὶ γνοὺς λέγει αὐτοῖς·</p><h1>JESUS </h1><h2>τί διαλογίζεσθε ὅτι ἄρτους οὐκ ἔχετε; οὔπω νοεῖτε οὐδὲ συνίετε; πεπωρωμένην ἔχετε τὴν καρδίαν ὑμῶν;<br />   <u>18</u>ὀφθαλμοὺς ἔχοντες οὐ βλέπετε<br />   καὶ ὦτα ἔχοντες οὐκ ἀκούετε;<br />   καὶ οὐ μνημονεύετε, <u>19</u>ὅτε τοὺς πέντε ἄρτους ἔκλασα εἰς τοὺς πεντακισχιλίους , πόσους κοφίνους κλασμάτων πλήρεις ἤρατε;</h2><p>λέγουσιν αὐτῷ·</p><h1>THE DISCIPLES </h1><h3>δώδεκα.</h3><h1>JESUS </h1><h2><u>20</u>ὅτε τοὺς ἑπτὰ εἰς τοὺς τετρακισχιλίους, πόσων σπυρίδων πληρώματα κλασμάτων ἤρατε;</h2><p>καὶ λέγουσιν αὐτῷ·</p><h1>THE DISCIPLES </h1><h3>ἑπτά.</h3><p><u>21</u>καὶ ἔλεγεν αὐτοῖς·</p><h1>JESUS </h1><h2>οὔπω συνίετε;</h2><p>   <u>22</u>Καὶ ἔρχονται εἰς Βηθσαϊδάν. Καὶ φέρουσιν αὐτῷ τυφλὸν καὶ παρακαλοῦσιν αὐτὸν ἵνα αὐτοῦ ἅψηται. <u>23</u>καὶ ἐπιλαβόμενος τῆς χειρὸς τοῦ τυφλοῦ ἐξήνεγκεν αὐτὸν ἔξω τῆς κώμης καὶ πτύσας εἰς τὰ ὄμματα αὐτοῦ, ἐπιθεὶς τὰς χεῖρας αὐτῷ ἐπηρώτα αὐτόν·</p><h1>JESUS </h1><h2>εἴ τι βλέπεις;</h2><p><u>24</u>καὶ ἀναβλέψας ἔλεγεν·</p><h1>A BLIND MAN OF BETHSAIDA </h1><h4>βλέπω τοὺς ἀνθρώπους ὅτι ὡς δένδρα ὁρῶ περιπατοῦντας.</h4><p><u>25</u>εἶτα πάλιν ἐπέθηκεν τὰς χεῖρας ἐπὶ τοὺς ὀφθαλμοὺς αὐτοῦ, καὶ διέβλεψεν καὶ ἀπεκατέστη καὶ ἐνέβλεπεν τηλαυγῶς ἅπαντα. <u>26</u>καὶ ἀπέστειλεν αὐτὸν εἰς οἶκον αὐτοῦ λέγων·</p><h1>JESUS </h1><h2>μηδὲ εἰς τὴν κώμην εἰσέλθῃς.</h2><p>   <u>27</u>Καὶ ἐξῆλθεν ὁ Ἰησοῦς καὶ οἱ μαθηταὶ αὐτοῦ εἰς τὰς κώμας Καισαρείας τῆς Φιλίππου· καὶ ἐν τῇ ὁδῷ ἐπηρώτα τοὺς μαθητὰς αὐτοῦ λέγων αὐτοῖς·</p><h1>JESUS </h1><h2>τίνα με λέγουσιν οἱ ἄνθρωποι εἶναι;</h2><p><u>28</u>οἱ δὲ εἶπαν αὐτῷ λέγοντεςὅτι</p><h1>THE DISCIPLES </h1><h3>Ἰωάννην τὸν βαπτιστήν, καὶ ἄλλοι Ἠλίαν, ἄλλοι δὲ ὅτι εἷς τῶν προφητῶν.</h3><p><u>29</u>καὶ αὐτὸς ἐπηρώτα αὐτούς·</p><h1>JESUS </h1><h2>ὑμεῖς δὲ τίνα με λέγετε εἶναι;</h2><p>ἀποκριθεὶς ὁ Πέτρος λέγει αὐτῷ·</p><h1>SIMON PETER </h1><h3>σὺ εἶ ὁ χριστός.</h3><p><u>30</u>καὶ ἐπετίμησεν αὐτοῖς ἵνα μηδενὶ λέγωσιν περὶ αὐτοῦ.<br />   <u>31</u>Καὶ ἤρξατο διδάσκειν αὐτοὺς ὅτι δεῖ τὸν υἱὸν τοῦ ἀνθρώπου πολλὰ παθεῖν καὶ ἀποδοκιμασθῆναι ὑπὸ τῶν πρεσβυτέρων καὶ τῶν ἀρχιερέων καὶ τῶν γραμματέων καὶ ἀποκτανθῆναι καὶ μετὰ τρεῖς ἡμέρας ἀναστῆναι· <u>32</u>καὶ παρρησίᾳ τὸν λόγον ἐλάλει. καὶ προσλαβόμενος ὁ Πέτρος αὐτὸν ἤρξατο ἐπιτιμᾶν αὐτῷ.<u>33</u>ὁ δὲ ἐπιστραφεὶς καὶ ἰδὼν τοὺς μαθητὰς αὐτοῦ ἐπετίμησεν Πέτρῳ καὶ λέγει·</p><h1>JESUS </h1><h2>ὕπαγε ὀπίσω μου, σατανᾶ, ὅτι οὐ φρονεῖς τὰ τοῦ θεοῦ ἀλλὰ τὰ τῶν ἀνθρώπων.</h2><p>   <u>34</u>Καὶ προσκαλεσάμενος τὸν ὄχλον σὺν τοῖς μαθηταῖς αὐτοῦ εἶπεν αὐτοῖς·</p><h1>JESUS </h1><h2>εἴ τις θέλει ὀπίσω μου ἀκολουθεῖν, ἀπαρνησάσθω ἑαυτὸν καὶ ἀράτω τὸν σταυρὸν αὐτοῦ καὶ ἀκολουθείτω μοι. <u>35</u>ὃς γὰρ ἐὰν θέλῃ τὴν ψυχὴν αὐτοῦ σῶσαι ἀπολέσει αὐτήν· ὃς δʼ ἂν ἀπολέσει τὴν ψυχὴν αὐτοῦ ἕνεκεν ἐμοῦ καὶ τοῦ εὐαγγελίου σώσει αὐτήν. <u>36</u>τί γὰρ ὠφελεῖ ἄνθρωπον κερδῆσαι τὸν κόσμον ὅλον καὶ ζημιωθῆναι τὴν ψυχὴν αὐτοῦ; <u>37</u>τί γὰρ δοῖ ἄνθρωπος ἀντάλλαγμα τῆς ψυχῆς αὐτοῦ; <u>38</u>ὃς γὰρ ἐὰν ἐπαισχυνθῇ με καὶ τοὺς ἐμοὺς λόγους ἐν τῇ γενεᾷ ταύτῃ τῇ μοιχαλίδι καὶ ἁμαρτωλῷ, καὶ ὁ υἱὸς τοῦ ἀνθρώπου ἐπαισχυνθήσεται αὐτόν, ὅταν ἔλθῃ ἐν τῇ δόξῃ τοῦ πατρὸς αὐτοῦ μετὰ τῶν ἀγγέλων τῶν ἁγίων.</h2><p>   <strong>9</strong><u>1</u>Καὶ ἔλεγεν αὐτοῖς·</p><h1>JESUS </h1><h2>ἀμὴν λέγω ὑμῖν ὅτι εἰσίν τινες ὧδε τῶν ἑστηκότων οἵτινες οὐ μὴ γεύσωνται θανάτου ἕως ἂν ἴδωσιν τὴν βασιλείαν τοῦ θεοῦ ἐληλυθυῖαν ἐν δυνάμει.</h2><p>   <u>2</u>Καὶ μετὰ ἡμέρας ἓξ παραλαμβάνει ὁ Ἰησοῦς τὸν Πέτρον καὶ τὸν Ἰάκωβον καὶ τὸν Ἰωάννην καὶ ἀναφέρει αὐτοὺς εἰς ὄρος ὑψηλὸν κατʼ ἰδίαν μόνους. καὶ μετεμορφώθη ἔμπροσθεν αὐτῶν, <u>3</u>καὶ τὰ ἱμάτια αὐτοῦ ἐγένετο στίλβοντα λευκὰ λίαν, οἷα γναφεὺς ἐπὶ τῆς γῆς οὐ δύναται οὕτως λευκᾶναι. <u>4</u>καὶ ὤφθη αὐτοῖς Ἠλίας σὺν Μωϋσεῖ καὶ ἦσαν συλλαλοῦντες τῷ Ἰησοῦ. <u>5</u>καὶ ἀποκριθεὶς ὁ Πέτρος λέγει τῷ Ἰησοῦ·</p><h1>SIMON PETER </h1><h3>ῥαββί, καλόν ἐστιν ἡμᾶς ὧδε εἶναι, καὶ ποιήσωμεν τρεῖς σκηνάς, σοὶ μίαν καὶ Μωϋσεῖ μίαν καὶ Ἠλίᾳ μίαν.</h3><p><u>6</u>οὐ γὰρ ᾔδει τί ἀποκριθῇ, ἔκφοβοι γὰρ ἐγένοντο. <u>7</u>καὶ ἐγένετο νεφέλη ἐπισκιάζουσα αὐτοῖς, καὶ ἐγένετο φωνὴ ἐκ τῆς νεφέλης·</p><h1>GOD </h1><h2>οὗτός ἐστιν ὁ υἱός μου ὁ ἀγαπητός, ἀκούετε αὐτοῦ.</h2><p><u>8</u>καὶ ἐξάπινα περιβλεψάμενοι οὐκέτι οὐδένα εἶδον ἀλλὰ τὸν Ἰησοῦν μόνον μεθʼ ἑαυτῶν.<br />   <u>9</u>Καὶ καταβαινόντων αὐτῶν ἐκ τοῦ ὄρους διεστείλατο αὐτοῖς ἵνα μηδενὶ ἃ εἶδον διηγήσωνται, εἰ μὴ ὅταν ὁ υἱὸς τοῦ ἀνθρώπου ἐκ νεκρῶν ἀναστῇ. <u>10</u>καὶ τὸν λόγον ἐκράτησαν πρὸς ἑαυτοὺς συζητοῦντες τί ἐστιν τὸ ἐκ νεκρῶν ἀναστῆναι.<br />   <u>11</u>Καὶ ἐπηρώτων αὐτὸν λέγοντες· ὅτι</p><h1>THREE DISCIPLES </h1><h3>λέγουσιν οἱ γραμματεῖς ὅτι Ἠλίαν δεῖ ἐλθεῖν πρῶτον;</h3><p><u>12</u>ὁ δὲ ἔφη αὐτοῖς·</p><h1>JESUS </h1><h2>Ἠλίας μὲν ἐλθὼν πρῶτον ἀποκαθιστάνει πάντα· καὶ πῶς γέγραπται ἐπὶ τὸν υἱὸν τοῦ ἀνθρώπου ἵνα πολλὰ πάθῃ καὶ ἐξουδενηθῇ; <u>13</u>ἀλλὰ λέγω ὑμῖν ὅτι καὶ Ἠλίας ἐλήλυθεν, καὶ ἐποίησαν αὐτῷ ὅσα ἤθελον, καθὼς γέγραπται ἐπʼ αὐτόν.</h2><p>   <u>14</u>Καὶ ἐλθόντες πρὸς τοὺς μαθητὰς εἶδον ὄχλον πολὺν περὶ αὐτοὺς καὶ γραμματεῖς συζητοῦντας πρὸς αὐτούς. <u>15</u>καὶ εὐθὺς πᾶς ὁ ὄχλος ἰδόντες αὐτὸν ἐξεθαμβήθησαν καὶ προστρέχοντες ἠσπάζοντο αὐτόν. <u>16</u>καὶ ἐπηρώτησεν αὐτούς·</p><h1>JESUS </h1><h2>τί συζητεῖτε πρὸς αὐτούς;</h2><p><u>17</u>Καὶ ἀπεκρίθη αὐτῷ εἷς ἐκ τοῦ ὄχλου·</p><h1>AN EPILEPTIC’S FATHER </h1><h4>διδάσκαλε, ἤνεγκα τὸν υἱόν μου πρὸς σέ, ἔχοντα πνεῦμα ἄλαλον·<u>18</u>καὶ ὅπου ἐὰν αὐτὸν καταλάβῃ ῥήσσει αὐτόν, καὶ ἀφρίζει καὶ τρίζει τοὺς ὀδόντας καὶ ξηραίνεται· καὶ εἶπα τοῖς μαθηταῖς σου ἵνα αὐτὸ ἐκβάλωσιν, καὶ οὐκ ἴσχυσαν.</h4><p><u>19</u>ὁ δὲ ἀποκριθεὶς αὐτοῖς λέγει·</p><h1>JESUS </h1><h2>ὦ γενεὰ ἄπιστος, ἕως πότε πρὸς ὑμᾶς ἔσομαι; ἕως πότε ἀνέξομαι ὑμῶν; φέρετε αὐτὸν πρός με.</h2><p><u>20</u>καὶ ἤνεγκαν αὐτὸν πρὸς αὐτόν. καὶ ἰδὼν αὐτὸν τὸ πνεῦμα εὐθὺς συνεσπάραξεν αὐτόν, καὶ πεσὼν ἐπὶ τῆς γῆς ἐκυλίετο ἀφρίζων. <u>21</u>καὶ ἐπηρώτησεν τὸν πατέρα αὐτοῦ·</p><h1>JESUS </h1><h2>πόσος χρόνος ἐστὶν ὡς τοῦτο γέγονεν αὐτῷ;</h2><p>ὁ δὲ εἶπεν·</p><h1>AN EPILEPTIC’S FATHER </h1><h4>ἐκ παιδιόθεν· <u>22</u>καὶ πολλάκις καὶ εἰς πῦρ αὐτὸν ἔβαλεν καὶ εἰς ὕδατα ἵνα ἀπολέσῃ αὐτόν· ἀλλʼ εἴ τι δύνῃ, βοήθησον ἡμῖν σπλαγχνισθεὶς ἐφʼ ἡμᾶς.</h4><p><u>23</u>ὁ δὲ Ἰησοῦς εἶπεν αὐτῷ·</p><h1>JESUS </h1><h2>τὸ εἰ δύνῃ, πάντα δυνατὰ τῷ πιστεύοντι.</h2><p><u>24</u>εὐθὺς κράξας ὁ πατὴρ τοῦ παιδίου ἔλεγεν·</p><h1>AN EPILEPTIC’S FATHER </h1><h4>πιστεύω· βοήθει μου τῇ ἀπιστίᾳ.</h4><p><u>25</u>Ἰδὼν δὲ ὁ Ἰησοῦς ὅτι ἐπισυντρέχει ὄχλος, ἐπετίμησεν τῷ πνεύματι τῷ ἀκαθάρτῳ λέγων αὐτῷ·</p><h1>JESUS </h1><h2>τὸ ἄλαλον καὶ κωφὸν πνεῦμα, ἐγὼ ἐπιτάσσω σοι, ἔξελθε ἐξ αὐτοῦ καὶ μηκέτι εἰσέλθῃς εἰς αὐτόν.</h2><p><u>26</u>καὶ κράξας καὶ πολλὰ σπαράξας ἐξῆλθεν· καὶ ἐγένετο ὡσεὶ νεκρός, ὥστε τοὺς πολλοὺς λέγειν ὅτι</p><h1>THE CROWD </h1><h4>ἀπέθανεν.</h4><p><u>27</u>ὁ δὲ Ἰησοῦς κρατήσας τῆς χειρὸς αὐτοῦ ἤγειρεν αὐτόν, καὶ ἀνέστη.<br />   <u>28</u>Καὶ εἰσελθόντος αὐτοῦ εἰς οἶκον οἱ μαθηταὶ αὐτοῦ κατʼ ἰδίαν ἐπηρώτων αὐτόν· ὅτι</p><h1>THE DISCIPLES </h1><h3>ἡμεῖς οὐκ ἠδυνήθημεν ἐκβαλεῖν αὐτό;</h3><p><u>29</u>καὶ εἶπεν αὐτοῖς·</p><h1>JESUS </h1><h2>τοῦτο τὸ γένος ἐν οὐδενὶ δύναται ἐξελθεῖν εἰ μὴ ἐν προσευχῇ.</h2><p>   <u>30</u>Κἀκεῖθεν ἐξελθόντες παρεπορεύοντο διὰ τῆς Γαλιλαίας, καὶ οὐκ ἤθελεν ἵνα τις γνοῖ· <u>31</u>ἐδίδασκεν γὰρ τοὺς μαθητὰς αὐτοῦ καὶ ἔλεγεν αὐτοῖς ὅτι</p><h1>JESUS </h1><h2>ὁ υἱὸς τοῦ ἀνθρώπου παραδίδοται εἰς χεῖρας ἀνθρώπων, καὶ ἀποκτενοῦσιν αὐτόν, καὶ ἀποκτανθεὶς μετὰ τρεῖς ἡμέρας ἀναστήσεται.</h2><p><u>32</u>οἱ δὲ ἠγνόουν τὸ ῥῆμα, καὶ ἐφοβοῦντο αὐτὸν ἐπερωτῆσαι.<br />   <u>33</u>Καὶ ἦλθον εἰς Καφαρναούμ. Καὶ ἐν τῇ οἰκίᾳ γενόμενος ἐπηρώτα αὐτούς·</p><h1>JESUS </h1><h2>τί ἐν τῇ ὁδῷ διελογίζεσθε;</h2><p><u>34</u>οἱ δὲ ἐσιώπων· πρὸς ἀλλήλους γὰρ διελέχθησαν ἐν τῇ ὁδῷ τίς μείζων. <u>35</u>καὶ καθίσας ἐφώνησεν τοὺς δώδεκα καὶ λέγει αὐτοῖς·</p><h1>JESUS </h1><h2>εἴ τις θέλει πρῶτος εἶναι, ἔσται πάντων ἔσχατος καὶ πάντων διάκονος.</h2><p><u>36</u>καὶ λαβὼν παιδίον ἔστησεν αὐτὸ ἐν μέσῳ αὐτῶν καὶ ἐναγκαλισάμενος αὐτὸ εἶπεν αὐτοῖς·</p><h1>JESUS </h1><h2><u>37</u>ὃς ἂν ἓν τῶν τοιούτων παιδίων δέξηται ἐπὶ τῷ ὀνόματί μου, ἐμὲ δέχεται· καὶ ὃς ἂν ἐμὲ δέχηται, οὐκ ἐμὲ δέχεται ἀλλὰ τὸν ἀποστείλαντά με.</h2><p>   <u>38</u>Ἔφη αὐτῷ ὁ Ἰωάννης·</p><h1>JOHN THE DISCIPLE </h1><h3>διδάσκαλε, εἴδομέν τινα ἐν τῷ ὀνόματί σου ἐκβάλλοντα δαιμόνια καὶ ἐκωλύομεν αὐτόν, ὅτι οὐκ ἠκολούθει ἡμῖν.</h3><p><u>39</u>ὁ δὲ Ἰησοῦς εἶπεν·</p><h1>JESUS </h1><h2>μὴ κωλύετε αὐτόν. οὐδεὶς γάρ ἐστιν ὃς ποιήσει δύναμιν ἐπὶ τῷ ὀνόματί μου καὶ δυνήσεται ταχὺ κακολογῆσαί με· <u>40</u>ὃς γὰρ οὐκ ἔστιν καθʼ ἡμῶν, ὑπὲρ ἡμῶν ἐστιν.<br />   <u>41</u>Ὃς γὰρ ἂν ποτίσῃ ὑμᾶς ποτήριον ὕδατος ἐν ὀνόματι ὅτι Χριστοῦ ἐστε, ἀμὴν λέγω ὑμῖν ὅτι οὐ μὴ ἀπολέσῃ τὸν μισθὸν αὐτοῦ.<br />   <u>42</u>Καὶ ὃς ἂν σκανδαλίσῃ ἕνα τῶν μικρῶν τούτων τῶν πιστευόντων εἰς ἐμέ, καλόν ἐστιν αὐτῷ μᾶλλον εἰ περίκειται μύλος ὀνικὸς περὶ τὸν τράχηλον αὐτοῦ καὶ βέβληται εἰς τὴν θάλασσαν. <u>43</u>Καὶ ἐὰν σκανδαλίζῃ σε ἡ χείρ σου, ἀπόκοψον αὐτήν· καλόν ἐστίν σε κυλλὸν εἰσελθεῖν εἰς τὴν ζωὴν ἢ τὰς δύο χεῖρας ἔχοντα ἀπελθεῖν εἰς τὴν γέενναν, εἰς τὸ πῦρ τὸ ἄσβεστον. <u>45</u>Καὶ ἐὰν ὁ πούς σου σκανδαλίζῃ σε, ἀπόκοψον αὐτόν· καλόν ἐστίν σε εἰσελθεῖν εἰς τὴν ζωὴν χωλὸν ἢ τοὺς δύο πόδας ἔχοντα βληθῆναι εἰς τὴν γέενναν. <u>47</u>Καὶ ἐὰν ὁ ὀφθαλμός σου σκανδαλίζῃ σε, ἔκβαλε αὐτόν· καλόν σέ ἐστιν μονόφθαλμον εἰσελθεῖν εἰς τὴν βασιλείαν τοῦ θεοῦ ἢ δύο ὀφθαλμοὺς ἔχοντα βληθῆναι εἰς τὴν γέενναν , <u>48</u>ὅπου ὁ σκώληξ αὐτῶν οὐ τελευτᾷ καὶ τὸ πῦρ οὐ σβέννυται.<br />   <u>49</u>Πᾶς γὰρ πυρὶ ἁλισθήσεται.<u>50</u>καλὸν τὸ ἅλας· ἐὰν δὲ τὸ ἅλας ἄναλον γένηται, ἐν τίνι αὐτὸ ἀρτύσετε; ἔχετε ἐν ἑαυτοῖς ἅλα καὶ εἰρηνεύετε ἐν ἀλλήλοις.</h2>","path":"/320","Question1":"Meditate on the acts of God described in this portion of Scripture.","Question2":"What do these clarify about the heart of God?","Question3":"Have you experienced this expression of the heart of God in your life? If yes, how so? If not, what steps can you take to experience this?","Question4":"What can you do to grow in your desire to know and obey God more intimately in your life?","prevPath":"/319","nextPath":"/321"}},"pathContext":{}}

